import { Room, Booking } from './types';
import { addHours, set, isWithinInterval, startOfDay, endOfDay } from 'date-fns';
import { PlaceHolderImages } from './placeholder-images';

const MOCK_USER_ID = 'user-123';

let rooms: Room[] = [
  { id: 'R1', name: 'The Innovator', capacity: 4, equipment: ['Whiteboard', 'TV'], imageUrl: PlaceHolderImages.find(p => p.id === 'room1')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room1')?.imageHint || '' },
  { id: 'R2', name: 'The Huddle', capacity: 2, equipment: ['TV'], imageUrl: PlaceHolderImages.find(p => p.id === 'room2')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room2')?.imageHint || '' },
  { id: 'R3', name: 'The Boardroom', capacity: 12, equipment: ['Projector', 'Conference Phone', 'Whiteboard'], imageUrl: PlaceHolderImages.find(p => p.id === 'room3')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room3')?.imageHint || '' },
  { id: 'R4', name: 'The Nook', capacity: 2, equipment: [], imageUrl: PlaceHolderImages.find(p => p.id === 'room4')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room4')?.imageHint || '' },
  { id: 'R5', name: 'The Collab', capacity: 6, equipment: ['Whiteboard', 'TV'], imageUrl: PlaceHolderImages.find(p => p.id === 'room5')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room5')?.imageHint || '' },
  { id: 'R6', name: 'War Room', capacity: 8, equipment: ['Projector', 'Whiteboard'], imageUrl: PlaceHolderImages.find(p => p.id === 'room6')?.imageUrl || '', imageHint: PlaceHolderImages.find(p => p.id === 'room6')?.imageHint || '' },
];

let bookings: Booking[] = [
  { id: 'B1', userId: 'user-456', userName: 'Jane Doe', userImage: 'https://picsum.photos/seed/user2/40/40', roomId: 'R1', startTime: set(new Date(), { hours: 10, minutes: 0, seconds: 0, milliseconds: 0 }), endTime: set(new Date(), { hours: 11, minutes: 0, seconds: 0, milliseconds: 0 }), title: 'Project Sync' },
  { id: 'B2', userId: MOCK_USER_ID, userName: 'John Smith', userImage: 'https://picsum.photos/seed/user1/40/40', roomId: 'R3', startTime: set(new Date(), { hours: 14, minutes: 0, seconds: 0, milliseconds: 0 }), endTime: set(new Date(), { hours: 15, minutes: 30, seconds: 0, milliseconds: 0 }), title: 'Quarterly Review' },
  { id: 'B3', userId: 'user-789', userName: 'Peter Jones', userImage: 'https://picsum.photos/seed/user3/40/40', roomId: 'R1', startTime: addHours(new Date(), 1), endTime: addHours(new Date(), 2), title: 'Design Standup' },
];

export const getRooms = (): Room[] => rooms;

export const getRoomById = (id: string): Room | undefined => rooms.find(r => r.id === id);

export const getBookings = (): Booking[] => bookings.map(b => ({ ...b, startTime: new Date(b.startTime), endTime: new Date(b.endTime) }));

export const getBookingsByRoom = (roomId: string): Booking[] => getBookings().filter(b => b.roomId === roomId);

export const getBookingsForDay = (date: Date): Booking[] => {
    const dayStart = startOfDay(date);
    const dayEnd = endOfDay(date);
    return getBookings().filter(b => isWithinInterval(b.startTime, { start: dayStart, end: dayEnd }));
}

export const getMyBookings = (): Booking[] => getBookings().filter(b => b.userId === MOCK_USER_ID).sort((a, b) => a.startTime.getTime() - b.startTime.getTime());

export const isRoomAvailable = (roomId: string, time: Date): boolean => {
  const roomBookings = getBookingsByRoom(roomId);
  return !roomBookings.some(b => isWithinInterval(time, { start: b.startTime, end: b.endTime }));
};

export const addBooking = (booking: Omit<Booking, 'id' | 'userId' | 'userName' | 'userImage'>): Booking => {
  const newBooking: Booking = {
    ...booking,
    id: `B${Date.now()}`,
    userId: MOCK_USER_ID,
    userName: 'John Smith',
    userImage: 'https://picsum.photos/seed/user1/40/40',
  };
  bookings.push(newBooking);
  return newBooking;
};

export const cancelBooking = (bookingId: string): boolean => {
  const index = bookings.findIndex(b => b.id === bookingId && b.userId === MOCK_USER_ID);
  if (index > -1) {
    bookings.splice(index, 1);
    return true;
  }
  return false;
};

export const updateBooking = (bookingId: string, updates: Partial<Booking>): Booking | null => {
    const index = bookings.findIndex(b => b.id === bookingId && b.userId === MOCK_USER_ID);
    if (index > -1) {
        bookings[index] = { ...bookings[index], ...updates };
        return bookings[index];
    }
    return null;
}
